import { JSX, ReactNode } from "react"
import { styled } from "@rp/react/theme"
import { Box, IconButton, Typography } from "@rp/react/web/mui/core"
import { Close } from "@rp/react/web/mui/icons"

const ModalHeaderStyled = styled("div")(({ theme }) => ({
  position: "relative",
  "& p": {
    fontSize: "1.5rem",
    fontWeight: 700,
  },
  [theme.breakpoints.down("sm")]: {
    "& p": {
      fontSize: "1.25rem",
    },
  },
}))

type ModalHeaderProps = {
  title?: ReactNode
  handleClose?: () => void
  titleIcon?: ReactNode
  titleMultiline?: boolean
}
const GenericModalHeader = ({
  title = "",
  titleIcon,
  handleClose,
  titleMultiline = false,
}: ModalHeaderProps): JSX.Element => (
  <ModalHeaderStyled>
    <Box sx={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
      <Box sx={{ flexGrow: 1, minWidth: 0 }}>
        <Typography
          sx={{
            maxWidth: titleIcon ? "calc(100% - 40px)" : "100%",
            whiteSpace: titleMultiline ? undefined : "nowrap",
            overflow: titleMultiline ? undefined : "hidden",
            textOverflow: titleMultiline ? undefined : "ellipsis",
          }}
        >
          {title}
        </Typography>
        {titleIcon}
      </Box>
      {handleClose ? (
        <IconButton role="close-btn" onClick={handleClose} aria-label="delete">
          <Close />
        </IconButton>
      ) : null}
    </Box>
  </ModalHeaderStyled>
)

export { GenericModalHeader, ModalHeaderStyled }
